.post {
    width: 100%;
    border-radius: 10px;
    -webkit-box-shadow: 0px 0px 16px -8px rgba(0, 0, 0, 0.68);
    box-shadow: 0px 0px 16px -8px rgba(0, 0, 0, 0.68);
    margin: 30px 0;

}

.postWrapper {
    padding: 10px;
    position: relative;
}

.linkClass {
    text-decoration: none;
    display: flex;
    justify-content: center;
    align-items: center;
    color: black
}

.postTop {
    display: flex;
    align-items: center;

}

.postTopLeft {
    display: flex;
    align-items: center;
    flex: 2;

}

.postProfileImg {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    object-fit: cover;
    cursor: pointer;
}

.linkClass .postUsername {
    font-size: 15px;
    font-weight: 500;
    margin: 0 10px;
    cursor: pointer;
}

.postDate {
    font-size: 12px;
}

.postMoreVert {
    cursor: pointer;
    margin-right: auto;
    flex: 1;

}

.postCenter {
    margin: 20px 0;
    position: relative;

}

.postTopRight{
    position: relative;
}
.postImg {
    margin-top: 20px;
    width: 100%;
    max-height: 500px;
    object-fit: contain;
}

.postBottom {
    display: flex;
    align-items: center;
    justify-content: space-between;

}

.postBottomLeft {
    display: flex;
    align-items: center;
}

.reactIcon {
    width: 20px;
    height: 20px;
    margin-right: 5px;
    cursor: pointer;
}


.postReactCounter {
    font-size: 13px;
    cursor: pointer;
}

.postCommentText {
    cursor: pointer;
    border-bottom: 1px dashed gray;
    font-size: 15px;

}